import { mapState} from 'vuex'

import TypeTabbar from '@/components/user/typetabbar.vue'
import GoodCard from '@/components/user/goodCard.vue'

export default {
	data(){
		return{
			goods:[],
			count:'',
			page:1,
			limit:8,
			loadingText:'',
			animationData1:{},
			animationData2:{}
		}
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey
		})
	},
	components:{
		TypeTabbar,
		// MyGoodInfo,
		GoodCard
	},
	methods:{
		getTabbar(data){
			if(data.typetabbar){
				this.getMessage()
			}else{
				this.getCard()
			}
		},
		getCard(){
			let animation1=uni.createAnimation({
				timingFunction:'ease-in-out',
				duration:500,
			})
			let animation2=uni.createAnimation({
				timingFunction:'ease-in-out',
				duration:500,
			})
			
			animation1.rotateY(-180).step();
			animation2.rotateY(0).step();
			
			this.animationData1=animation1.export();
			this.animationData2=animation2.export();
		},
		getMessage(){
			let animation1=uni.createAnimation({
				timingFunction:'ease-in-out',
				duration:500,
			})
			let animation2=uni.createAnimation({
				timingFunction:'ease-in-out',
				duration:500,
			})
			animation1.rotateY(0).step();
			animation2.rotateY(-180).step();
			
			this.animationData1=animation1.export();
			this.animationData2=animation2.export();
		},
		remove(id){
			let that=this;
			this.$api.mydele({
				id:id,
				page:that.page,
				limit:that.limit,
				sessionKey:that.sessionKey
			}).then(res=>{
				that.goods=res.goods;
				uni.showToast({
					title:res.msg
				})
			})
		},
		toInfo(id){
			console.log(id)
			uni.navigateTo({
				url:'../Info/Info?id='+id
			})
		},
		getAllData(){
			let that=this;
			uni.showLoading({
				title:'加载中...'
			})
			this.$api.mypublish({
				sessionKey:that.sessionKey,
				page:that.page,
				limit:that.limit
			}).then(res=>{
				that.count=res.count
				that.goods=res.goods
				uni.hideLoading();
			})
		},
		getMoreData(){
			let that=this;
			this.loadingText='加载更多...'
			that.page++;
			uni.showNavigationBarLoading();
			this.$api.mypublish({
				sessionKey:that.sessionKey,
				page:that.page,
				limit:that.limit
			}).then(res=>{
				if(res.goods.length===0){
					uni.hideNavigationBarLoading();
					that.loadingText='已加载完全部';
					that.page--;
					return false;
				}else{
					that.goods=that.goods.concat(res.goods)
					uni.hideNavigationBarLoading();
				}
			})
		}
	},
	onLoad() {
		this.getAllData()
	},
	onReachBottom() {
		setTimeout(()=>{
			this.getMoreData()
		},500)
	},
	onPullDownRefresh() {
		this.goods=[];
		this.page=1;
		this.getAllData();
	}
}
